import React, {useEffect} from 'react';
import Modal from "react-bootstrap/Modal";
import Button from "react-bootstrap/Button";
import {connect} from "react-redux";
import {updateAppSettings} from "src/actions/foundationActions";
import {loadNewMod} from "src/utilities/utilityFunctions";
import store from 'src/store';

const GenericModalDefault  = (props) => {
    let stateData = store.getState();
    let currentModName =stateData.appSettings.modalModuleName;
    useEffect(()=>{
        if(currentModName){
            loadNewMod(currentModName,(module)=>store.dispatch(updateAppSettings({modalModule:module.default})));
        }
    },[currentModName])

    let modalTitle= stateData.appSettings?.modalTitle ? stateData.appSettings.modalTitle : null;
    let modMod=stateData.appSettings.modalModule ? <stateData.appSettings.modalModule /> : null;

       let dismisser={};
        dismisser[props.triggerName]=false;
        return(
            <Modal id="genericModal" show={props.appSettings[props.triggerName]} onHide={() => store.dispatch(updateAppSettings(dismisser))} size='xl'>
                <Modal.Header closeButton>
                    <Modal.Title>{modalTitle}</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    {modMod}
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={() =>{store.dispatch(updateAppSettings(dismisser))}}>
                        Close
                    </Button>

                </Modal.Footer>
            </Modal>
        )
};

const mapStateToProps = (state) =>{
    return  {
        appSettings:state.appSettings,
    };
};

export default connect(mapStateToProps,{})(GenericModalDefault);
